import React, { useRef } from 'react'
import styled from 'styled-components'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import i18next from '../../i18n'

// import { ExternalLink } from '../../theme'
// import { useTranslation } from 'react-i18next'

const StyledLanguageButton = styled.div`
  height: 35px;
  line-height: 35px;
  border-radius: 0.5rem;
  color: ${({ theme }) => theme.text2};
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  svg {
    margin-top: 2px;
  }
`

const StyledLanguage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const LanguageFlyout = styled.span`
  min-width: 8.125rem;
  background-color: #fff;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 12px;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 4rem;
  right: 0rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
  `};
`

const LanguageItem = styled.div`
  flex: 1;
  line-height: 36px;
  padding: 0 20px;
  color: #606266;
  font-size: 14px;
  text-align: center
  :hover {
    background-color: #e5fffc;
    color: #188277;
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
  &.active {
    color: #08b8a3;
  }
`

// const CODE_LINK = 'https://github.com/Uniswap/uniswap-interface'

export default function Language() {
  // const { account } = useActiveWeb3React()

  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.LANGUAGE)
  const toggle = useToggleModal(ApplicationModal.LANGUAGE)
  useOnClickOutside(node, open ? toggle : undefined)
  // const { t } = useTranslation()
  // const openClaimModal = useToggleModal(ApplicationModal.ADDRESS_CLAIM)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledLanguage ref={node as any}>
      <StyledLanguageButton onClick={toggle}>
        {i18next.language === 'en' ? (
          <span>English</span>
        ) : (
          <span>中文</span>
        )}
      </StyledLanguageButton>

      {open && (
        <LanguageFlyout>
          <LanguageItem className={i18next.language === 'zh-CN' ? 'active' : ''} onClick={() => i18next.changeLanguage('zh-CN')}>
            中文
          </LanguageItem>
          <LanguageItem className={i18next.language === 'en' ? 'active' : ''} onClick={() => i18next.changeLanguage('en')}>
            English
          </LanguageItem>
        </LanguageFlyout>
      )}
    </StyledLanguage>
  )
}
