import React from 'react'
import styled from 'styled-components'

export const BodyWrapper = styled.div`
  position: relative;
  max-width: 480px;
  width: 100%;
  /* padding: 1rem; */
`
export const BodyWrapperMargin = styled.div`
  ${({ theme }) => theme.border1};
  margin-left: -15px;
  margin-right: -15px;
  border-radius: 30px;
`
/**
 * The styled container element that wraps the content of most pages and the tabs.
 */
export default function AppBody({ children }: { children: React.ReactNode }) {
  return <BodyWrapper><BodyWrapperMargin>{children}</BodyWrapperMargin></BodyWrapper>
}
